'use strict';

// MAIN //

/**
* Returns the maximum value.
*
* @private
* @param {NonNegativeInteger} x - first value
* @param {NonNegativeInteger} y - second value
* @returns {NonNegativeInteger} maximum value
*
* @example
* var v = max( 2, 3 );
* // returns 3
*
* @example
* var v = max( 3, 2 );
* // returns 3
*
* @example
* var v = max( 2, 2 );
* // returns 2
*/
function max( x, y ) {
	return ( x < y ) ? y : x;
}


// EXPORTS //

export default max;
