'use strict';

// MODULES //

import enum2state from './enum2state.json';
import closed from './closed.js'; // eslint-disable-line stdlib/no-redeclare
import comment from './comment.js';
import error from './error.js';
import escape from './escape.js'; // eslint-disable-line stdlib/no-redeclare
import field from './field.js';
import init from './init.js';
import invalidQuoteEnd from './invalid_quote_end.js';
import quoteEnd from './quote_end.js';
import quotedEscape from './quoted_escape.js';
import quotedField from './quoted_field.js';
import skip from './skip.js';
import skippedComment from './skipped_comment.js';
import skippedEscape from './skipped_escape.js';
import skippedField from './skipped_field.js';
import skippedInvalidQuoteEnd from './skipped_invalid_quote_end.js';
import skippedQuoteEnd from './skipped_quote_end.js';
import skippedQuotedEscape from './skipped_quoted_escape.js';
import skippedQuotedField from './skipped_quoted_field.js';


// VARIABLES //

var table = {
	'closed': closed,
	'comment': comment,
	'escape': escape,
	'error': error,
	'field': field,
	'init': init,
	'invalid_quote_end': invalidQuoteEnd,
	'quote_end': quoteEnd,
	'quoted_escape': quotedEscape,
	'quoted_field': quotedField,
	'skip': skip,
	'skipped_comment': skippedComment,
	'skipped_escape': skippedEscape,
	'skipped_field': skippedField,
	'skipped_invalid_quote_end': skippedInvalidQuoteEnd,
	'skipped_quote_end': skippedQuoteEnd,
	'skipped_quoted_escape': skippedQuotedEscape,
	'skipped_quoted_field': skippedQuotedField
};


// MAIN //

/**
* Returns an object mapping state enumeration constants to state functions.
*
* @private
* @param {Parser} parser - parser instance
* @returns {Array<Function>} table
*/
function states( parser ) {
	var out;
	var i;

	out = [];
	for ( i = 0; i < enum2state.length; i++ ) {
		out.push( table[ enum2state[ i ] ]( parser ) );
	}
	return out;
}


// EXPORTS //

export default states;
